
// Listen for click of button
document.getElementById('generate').addEventListener('click', performAction);

function performAction(e) {
  e.preventDefault();
  const destCity = document.getElementById('userCity').value;
  // put formChecker here
  fetch('http://localhost:9090/destination', {
    method: 'POST',
    credentials: 'same-origin',
    mode: 'cors',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify({destCity}),
    })
  .then(response => {
    console.log("/destination fetch done")
    return response.json()
  })
  .then(response => {
    console.log("Updating UI")
    document.getElementById('results').innerHTML = `
      <h2>Hooray! You're going to ${response.city}, ${response.country}</h2>
      <p>Your trip is XX days away</p>
      <h3>Current weather</h3>
      <p><img src="https://www.weatherbit.io/static/img/icons/${response.currentIcon}.png"> ${response.currentDescription}, ${response.currentTemp} C</p>
    `
  })
  .catch(error => console.log("/destination fetch error", error));
}

export {performAction}
